.workspace-row {
  color: #f5f5f5;
  background-color: #171717;
  border-radius: 6px;
  height: 60px;
  padding-top: 8px;
}

.icon-avatar {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  background-color: #a8a29e;
}

.page-tab-active {
  background-color: #04D939;
}

.nft-obj-img {
  border: 4px solid #03A63C;
  border-radius: 2px;
}

.row-page-btn {
  background-color: #03A63C;
  width: 90px;
  color: #fff;
  border-radius: 6px;
  margin-right: 14px;
}